.mexplorermenu-pull-left {
  float: left;
}

.mexplorermenu-pull-right {
  float: right;
}

.mexplorermenu-hidden {
  display: none;
}

.mexplorermenu-collapse {
  height: 0 !important;
}

.mexplorermenu-container {
  width: 100%;
  min-height: 27px;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: thin;
  border: 1px solid #dee2e6;
}

.mexplorermenu-header {
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 4px 8px 4px 8px;
  min-height: 27px;
  font-weight: 600;
  color: #526161;
  background-color: #dce6e6;
  z-index: 2;
}

.mexplorermenu-header > .mexplorermenu-title {
  flex: 1;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.mexplorermenu-header .mexplorermenu-icon {
  margin-right: 4px;
  width: 12px;
  height: 12px;
}

.mexplorermenu-header .mexplorermenu-btn {
  display: flex;
  align-items: center;
}

.mexplorermenu-header .mexplorermenu-btn > .mexplorermenu-icon {
  margin: 0 2px;
}

.mexplorermenu-header .mexplorermenu-btn > .mexplorermenu-img {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin: 0 2px;
  background-repeat: no-repeat;
  background-size: contain;
}

.mexplorermenu-header .mexplorermenu-icon:hover,
.mexplorermenu-header .mexplorermenu-img:hover {
  cursor: pointer;
}

.mexplorermenu-header .mexplorermenu-icon.fa-exchange-alt.rotate {
  transform: rotate(90deg);
}

.mexplorermenu-body {
  position: relative;
  top: 27px;
  padding: 0;
  margin: 0;
  background-color: #fff;
}

.mexplorermenu-body::-webkit-scrollbar {
  width: 7px;
  margin-right: 2px;
}

.mexplorermenu-body::-webkit-scrollbar:horizontal {
  height: 7px;
}

.mexplorermenu-body::-webkit-scrollbar-track, .mexplorermenu-body::-webkit-scrollbar-track:horizontal {
  outline: none;
}

.mexplorermenu-body::-webkit-scrollbar-thumb, .mexplorermenu-body::-webkit-scrollbar-thumb:horizontal {
  background-color: #dee2e6;
  outline: 0 solid #687685;
  border-radius: 4px;
}

.mexplorermenu-filter {
  position: relative;
  display: inline-block;
  width: 100%;
}

.mexplorermenu-filter .mexplorermenu-filter-input {
  width: 100%;
  height: 27px;
  position: relative;
  padding: 0 24px 0 8px;
  border: none;
  border-bottom: 1px solid #dee2e6;
  border-radius: 0;
}

.mexplorermenu-filter .mexplorermenu-filter-input::-ms-clear {
  /* Remove IE default X */
  display: none;
}

.mexplorermenu-filter .mexplorermenu-filter-clear-btn {
  display: none;
  position: absolute;
  right: 0;
  top: 2px;
  padding: 0 4px;
  color: #666;
  font-style: normal;
  font-size: 1.2em;
  user-select: none;
}

.mexplorermenu-filter .mexplorermenu-filter-clear-btn:hover {
  color: #526161;
  font-weight: bold;
  cursor: pointer;
}

.mexplorermenu-item {
  display: block;
  margin: 0 0 0 -8px;
  padding: 4px 4px 4px 24px;
  color: #212529;
  border-bottom: 1px solid #dee2e6;
  list-style: none;
}

.mexplorermenu-item.active {
  background-color: #e3e6e6;
}

.mexplorermenu-item:hover:not(.active) {
  background-color: #f8f9fa;
}

.mexplorermenu-item:hover {
  cursor: pointer;
}

.mexplorermenu-item .mexplorermenu-item-name {
  display: flex;
  align-items: center;
  // padding-left: 8px;
}

.mexplorermenu-item-name .mexplorermenu-item-img {
  margin-right: 4px;
  pointer-events: none;
}

.mexplorermenu-item-name .mexplorermenu-item-img > img {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  border-style: none;
}

.mexplorermenu-item-name .mexplorermenu-item-btn {
  margin-left: auto;
  padding: 0 4px;
}

.mexplorermenu-item-btn > i {
  visibility: hidden;
  color: #666;
}

.mexplorermenu-item-btn:hover {
  cursor: pointer;
}

.mexplorermenu-item:hover .mexplorermenu-item-btn > i {
  visibility: visible;
  cursor: pointer;
}

.mexplorermenu-item .mexplorermenu-item-btn.context-menu-active > i {
  visibility: visible;
}

.mexplorermenu-item-name .mexplorermenu-item-title {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: middle;
  white-space: nowrap;
}

.mexplorermenu-item-title + .mexplorermenu-item-info {
  margin: 0 4px;
}

.mexplorermenu-item-info.context-menu-active > i {
  visibility: visible;
}

.mexplorermenu-item-info.mexplorermenu-pull-right {
  margin-left: auto;
}

.mexplorermenu-item-info.mexplorermenu-pull-right + .mexplorermenu-item-btn {
  margin-left: 4px;
}

.mexplorermenu-item-info .mexplorermenu-tooltip {
  color: #666;
}

.mexplorermenu-tooltip:hover {
  cursor: pointer;
}

.mexplorermenu-item-group {
  display: block;
  margin: 0;
  padding: 0;
  color: #212529;
  list-style: none;
}

.mexplorermenu-item-group .mexplorermenu-item {
  margin-left: -16px;
  padding-left: 16px;
}

.mexplorermenu-item-group > .mexplorermenu-item-name:hover {
  cursor: pointer;
}

.mexplorermenu-item-group > .mexplorermenu-item-name {
  margin-left: -8px;
  padding: 4px 4px 4px 24px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #dee2e6;
}

.mexplorermenu-item-group .mexplorermenu-item-icon {
  margin-right: 4px;
  width: 16px;
  text-align: center;
}

.mexplorermenu-item-group .mexplorermenu-item-btn.context-menu-active > i {
  visibility: visible;
}

.mexplorermenu-item-group > .mexplorermenu-item:hover > .mexplorermenu-item-btn > i {
  visibility: visible;
  cursor: pointer;
}

.mexplorermenu-item-group > .mexplorermenu-item-name:hover .mexplorermenu-item-btn > i {
  visibility: visible;
  cursor: pointer;
}

.mexplorermenu-content {
  margin-left: 8px;
  padding: 0px;
  list-style: none;
}

.mexplorermenu-content > .mexplorermenu-item-group > .mexplorermenu-item-name {
  margin-left: -16px;
  padding-left: 32px;
}

.mexplorermenu-item-group .mexplorermenu-content > .mexplorermenu-item {
  margin-left: -16px;
  padding-left: 32px;
}

.mexplorermenu-body > ul > .mexplorermenu-item-group > .mexplorermenu-item-name {
  background-color: #f3f7f7;
  position: sticky;
  top: 27px;
}

/* context-menu-list */
.context-menu-list {
  position: absolute;
  display: inline-block;
  min-width: 160px;
  max-width: 160px;
  padding: 4px 0;
  margin: 0;
  list-style-type: none;
  background: #fff;
  border: 1px solid #dee2e6;
  border-radius: 3px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, .25);
}

.context-menu-list > .context-menu-item {
  padding: 4px 0 4px 32px;
  font-size: 12px;
  font-weight: normal;
  color: #666;
}

.context-menu-list > .context-menu-item.context-menu-hover {
  color: #666;
  background-color: #f8f9fa;
}

.context-menu-list > .context-menu-item.context-menu-separator {
  padding: 0;
  margin: .35em 0;
  border-bottom: 1px solid #dee2e6;
}

.context-menu-list > .context-menu-item.context-menu-disabled {
  color: #a9a9a9;
}

.context-menu-item.context-menu-disabled::after {
  border-color: #0000 #0000 #0000 #a9a9a9;
}

.context-menu-icon {
  display: inline-flex;
  width: 14px;
  height: 14px;
  position: relative;
  top: 2px;
  margin-right: 4px;
  background-repeat: no-repeat;
  background-size: contain;
}

.context-menu-icon.context-menu-hover:before,
.context-menu-icon.context-menu-icon--fa:before,
.context-menu-icon.context-menu-icon--fa.context-menu-hover:before {
  padding-left: 16px;
  padding-right: 4px;
  width: 12px;
  color: #666;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
}
