.mtreemenu-pull-left {
  float: left;
}

.mtreemenu-pull-right {
  float: right;
}

.mtreemenu-hidden {
  display: none;
}

.mtreemenu-collapse {
  height: 0 !important;
}

.mtreemenu-container {
  width: 100%;
  min-height: 27px;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: thin;
  border: 1px solid #dee2e6;
}

.mtreemenu-header {
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 4px 8px 4px 8px;
  min-height: 27px;
  font-weight: 600;
  color: #526161;
  background-color: #dce6e6;
  z-index: 2;
}

.mtreemenu-header > .mtreemenu-title {
  flex: 1;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.mtreemenu-header .mtreemenu-icon {
  margin-right: 4px;
  width: 12px;
  height: 12px;
}

.mtreemenu-header .mtreemenu-btn {
  display: flex;
  align-items: center;
}

.mtreemenu-header .mtreemenu-btn > .mtreemenu-icon {
  margin: 0 2px;
}

.mtreemenu-header .mtreemenu-btn > .mtreemenu-img {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin: 0 2px;
  background-repeat: no-repeat;
  background-size: contain;
}

.mtreemenu-header .mtreemenu-icon:hover,
.mtreemenu-header .mtreemenu-img:hover {
  cursor: pointer;
}

.mtreemenu-header .mtreemenu-icon.fa-exchange-alt.rotate {
  transform: rotate(90deg);
}

.mtreemenu-body {
  position: relative;
  top: 27px;
  padding: 0;
  margin: 0;
  background-color: #fff;
}

.mtreemenu-body::-webkit-scrollbar {
  width: 7px;
  margin-right: 2px;
}

.mtreemenu-body::-webkit-scrollbar:horizontal {
  height: 7px;
}

.mtreemenu-body::-webkit-scrollbar-track, .mtreemenu-body::-webkit-scrollbar-track:horizontal {
  outline: none;
}

.mtreemenu-body::-webkit-scrollbar-thumb, .mtreemenu-body::-webkit-scrollbar-thumb:horizontal {
  background-color: #dee2e6;
  outline: 0 solid #687685;
  border-radius: 4px;
}

.mtreemenu-noheader-body {
  position: relative;
  top: 0;
  padding: 0;
  margin: 0;
  background-color: #fff;
}

.mtreemenu-noheader-body::-webkit-scrollbar {
  width: 7px;
  margin-right: 2px;
}

.mtreemenu-noheader-body::-webkit-scrollbar:horizontal {
  height: 7px;
}

.mtreemenu-noheader-body::-webkit-scrollbar-track, .mtreemenu-noheader-body::-webkit-scrollbar-track:horizontal {
  outline: none;
}

.mtreemenu-noheader-body::-webkit-scrollbar-thumb, .mtreemenu-noheader-body::-webkit-scrollbar-thumb:horizontal {
  background-color: #dee2e6;
  outline: 0 solid #687685;
  border-radius: 4px;
}

.mtreemenu-filter {
  position: relative;
  display: inline-block;
  width: 100%;
}

.mtreemenu-filter .mtreemenu-filter-input {
  width: 100%;
  height: 27px;
  position: relative;
  padding: 0 24px 0 8px;
  border: none;
  border-bottom: 1px solid #dee2e6;
  border-radius: 0;
}

.mtreemenu-filter .mtreemenu-filter-input::-ms-clear {
  /* Remove IE default X */
  display: none;
}

.mtreemenu-filter .mtreemenu-filter-clear-btn {
  display: none;
  position: absolute;
  right: 0;
  top: 2px;
  padding: 0 4px;
  color: #666;
  font-style: normal;
  font-size: 1.2em;
  user-select: none;
}

.mtreemenu-filter .mtreemenu-filter-clear-btn:hover {
  color: #526161;
  font-weight: bold;
  cursor: pointer;
}

.mtreemenu-item {
  display: block;
  margin: 0 0 0 -8px;
  padding: 4px 4px 4px 24px;
  color: #212529;
  border-bottom: 1px solid #dee2e6;
  list-style: none;
}

.mtreemenu-item.active {
  background-color: #e3e6e6;
}

.mtreemenu-item:hover:not(.active) {
  background-color: #f8f9fa;
}

.mtreemenu-item:hover {
  cursor: pointer;
}

.mtreemenu-item .mtreemenu-item-name {
  display: flex;
  align-items: center;
  // padding-left: 8px;
}

.mtreemenu-item-name .mtreemenu-item-img {
  margin-right: 4px;
  pointer-events: none;
}

.mtreemenu-item-name .mtreemenu-item-img > img {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  border-style: none;
}

.mtreemenu-item-name .mtreemenu-item-btn {
  margin-left: auto;
  padding: 0 4px;
}

.mtreemenu-item-btn > i {
  visibility: hidden;
  color: #666;
}

.mtreemenu-item-btn:hover {
  cursor: pointer;
}

.mtreemenu-item:hover .mtreemenu-item-btn > i {
  visibility: visible;
  cursor: pointer;
}

.mtreemenu-item .mtreemenu-item-btn.context-menu-active > i {
  visibility: visible;
}

.mtreemenu-item-name .mtreemenu-item-title {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: middle;
  white-space: nowrap;
  width: 100%;
}

.mtreemenu-item-title + .mtreemenu-item-info {
  margin: 0 4px;
}

.mtreemenu-item-info.context-menu-active > i {
  visibility: visible;
}

.mtreemenu-item-info.mtreemenu-pull-right {
  margin-left: auto;
}

.mtreemenu-item-info.mtreemenu-pull-right + .mtreemenu-item-btn {
  margin-left: 4px;
}

.mtreemenu-item-info .mtreemenu-tooltip {
  color: #666;
}

.mtreemenu-tooltip:hover {
  cursor: pointer;
}

.mtreemenu-item-group {
  display: block;
  margin: 0;
  padding: 0;
  color: #212529;
  list-style: none;
}

.mtreemenu-item-group .mtreemenu-item {
  margin-left: -16px;
  padding-left: 16px;
}

.mtreemenu-item-group > .mtreemenu-item-name:hover {
  cursor: pointer;
}

.mtreemenu-item-group > .mtreemenu-item-name {
  margin-left: -8px;
  padding: 4px 4px 4px 16px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #dee2e6;
}

.mtreemenu-item-group .mtreemenu-item-icon {
  margin-right: 4px;
  width: 16px;
  text-align: center;
}

.mtreemenu-item-group .mtreemenu-item-btn.context-menu-active > i {
  visibility: visible;
}

.mtreemenu-item-group > .mtreemenu-item:hover > .mtreemenu-item-btn > i {
  visibility: visible;
  cursor: pointer;
}

.mtreemenu-item-group > .mtreemenu-item-name:hover .mtreemenu-item-btn > i {
  visibility: visible;
  cursor: pointer;
}

.mtreemenu-content {
  margin-left: 8px;
  padding: 0px;
  list-style: none;
}

.mtreemenu-content > .mtreemenu-item-group > .mtreemenu-item-name {
  margin-left: -16px;
  padding-left: 32px;
}

.mtreemenu-item-group .mtreemenu-content > .mtreemenu-item {
  margin-left: -16px;
  padding-left: 32px;
}

.mtreemenu-body > ul > .mtreemenu-item-group > .mtreemenu-item-name {
  background-color: #f3f7f7;
  position: sticky;
  top: 27px;
}

/* context-menu-list */
.context-menu-list {
  position: absolute;
  display: inline-block;
  min-width: 200px;
  max-width: 500px;
  padding: 4px 0;
  margin: 0;
  list-style-type: none;
  background: #fff;
  border: 1px solid #dee2e6;
  border-radius: 3px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, .25);
}

.context-menu-list > .context-menu-item {
  padding: 4px 4px 4px 8px;
  font-size: 12px;
  font-weight: normal;
  color: #666;
}

.context-menu-list > .context-menu-item.context-menu-hover {
  color: #666;
  background-color: #f8f9fa;
}

.context-menu-list > .context-menu-item.context-menu-separator {
  padding: 0;
  margin: .35em 0;
  border-bottom: 1px solid #dee2e6;
}

.context-menu-list > .context-menu-item.context-menu-disabled {
  color: #a9a9a9;
}

.context-menu-item.context-menu-disabled::after {
  border-color: #0000 #0000 #0000 #a9a9a9;
}

.context-menu-icon {
  display: inline-flex;
  width: 14px;
  height: 14px;
  position: relative;
  top: 2px;
  margin-right: 4px;
  background-repeat: no-repeat;
  background-size: contain;
}

.context-menu-icon.context-menu-hover:before,
.context-menu-icon.context-menu-icon--fa:before,
.context-menu-icon.context-menu-icon--fa.context-menu-hover:before {
  padding-left: 16px;
  padding-right: 4px;
  width: 12px;
  color: #666;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
}

.mtreemenu-loader {
  position: absolute;
  top:0px;
  right:0px;
  width:100%;
  height:100%;
  background-color:#fff;
  background-image:url('../img/loader-green.gif');
  background-size: 50px;
  background-repeat:no-repeat;
  background-position:center;
  z-index:10000000;
  opacity: 0.5;
  filter: alpha(opacity=40);
}
