.colselector-banned-action {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.7;
}

.colselector-select, .colselector-input {
  height: 20px;
  width: 200px;
  padding: 0 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.colselector-select-sm, .colselector-input-sm {
  width: 160px;
}

.colselector-select-lg, .colselector-input-lg {
  width: 240px;
}

.colselector-container {
  padding: 2px 4px;
  width: 268px;
  border: 1px solid #a9a9a9;
}

.colselector {
  width: 100%;
  height: 100%;
}

.colselector-header .colselector-title {
  padding: 2px 0;
  text-align: center;
}

.colselector-item {
  line-height: 20px;
}

.colselector-item .colselector-td {
  padding: 2px 0;
}

.colselector-btn {
  padding: 2px 4px;
  font-size: 12px;
  color: #666;
  border: none;
  border-radius: 3px;
}

.colselector-btn:hover {
  background-color: #e3e6e6;
}

.colselector-btn:active {
  background-color: #dee2e6;
}
