
.banned-action {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.3;
}

.minput-select, .minput-input {
  height: 20px;
  width: 100px;
  padding: 0 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.minput-input-sm {
  width: 40px;
}

.minput-container {
  padding: 2px 4px;
  margin: 0;
  width: 268px;
  border: 1px solid #a9a9a9;
}

.minput {
  width: 100%;
  height: 100%;
}

.minput-header .minput-title {
  padding: 2px 0;
  text-align: center;
}

.minput-item {
  line-height: 20px;
}

.minput-item .minput-td {
  padding: 2px 0;
}

.minput-btn {
  padding: 2px 4px;
  font-size: 12px;
  color: #666;
  border: none;
  border-radius: 3px;
}

.minput-btn:hover {
  background-color: #e3e6e6;
}

.minput-btn:active {
  background-color: #dee2e6;
}
