.munitmenu-pull-left { float: left; }

.munitmenu-pull-right { float: right; }

.munitmenu-hidden { display: none; }

.munitmenu-collapse { height: 0 !important; }

.munitmenu-container {
  min-height: 27px;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: thin;
  border: 1px solid #dee2e6;
}

.munitmenu-container:not(:last-child) {
  margin-bottom: 12px;
}

.munitmenu-header {
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 2px 8px 2px 8px;
  font-weight: 600;
  color: #526161;
  background-color: #dce6e6;
  z-index: 1;
}

.munitmenu-header > .munitmenu-title {
  flex: 1;
}

.munitmenu-header .munitmenu-icon {
  margin-right: 4px;
  width: 12px;
  height: 12px;
}

.munitmenu-header .munitmenu-btn > .munitmenu-icon {
  margin: 0 2px;
}

.munitmenu-header .munitmenu-icon:hover {
  cursor: pointer;
}

.munitmenu-body {
  position: relative;
  top: 27px;
  padding: 0;
  margin: 0;
  background-color: #fff;
}

.munitmenu-body::-webkit-scrollbar {
  width: 7px;
  margin-right: 2px;
}

.munitmenu-body::-webkit-scrollbar:horizontal {
  height: 7px;
}

.munitmenu-body::-webkit-scrollbar-track, .munitmenu-body::-webkit-scrollbar-track:horizontal {
  outline: none;
}

.munitmenu-body::-webkit-scrollbar-thumb, .munitmenu-body::-webkit-scrollbar-thumb:horizontal {
  background-color: #dee2e6;
  outline: 0 solid #687685;
  border-radius: 4px;
}

.munitmenu-body > .munitmenu-item {
  display: block;
  padding: 2px 8px 2px 20px;
  color: #212529;
  list-style: none;
}

.munitmenu-body .munitmenu-item:not(.munitmenu-item-group) {
  border-bottom: 1px solid #dee2e6;
}

.munitmenu-body .munitmenu-item:not(.munitmenu-item-group):hover:not(.active) {
  background-color: #f8f9fa;
}

.munitmenu-body > .munitmenu-item.munitmenu-item-group,
.munitmenu-body > .munitmenu-item.munitmenu-item-group > .munitmenu-content {
  list-style: none;
  padding: 0;
  margin: 0;
}

.munitmenu-item:not(.munitmenu-item-group) .munitmenu-item-title {
  display: inline-block;
  max-width: 165px;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: middle;
  white-space: nowrap;
}

.munitmenu-item.active {
  background-color: #e3e6e6;
}

.munitmenu-item.munitmenu-item-group > .munitmenu-item-name {
  display: block;
  position: sticky;
  top: 0;
  padding: 2px 8px 2px 20px;
  background-color: #f3f7f7;
  border-bottom: 1px solid #dee2e6;
  list-style: none;
}

.munitmenu-item.munitmenu-item-group .munitmenu-item-icon {
  margin-right: 4px;
  vertical-align: middle;
}

.munitmenu-item:hover,
.munitmenu-item.munitmenu-item-group .munitmenu-item-icon:hover,
.munitmenu-item.munitmenu-item-group .munitmenu-item-btn > i:hover {
  cursor: pointer;
}

.munitmenu-item.munitmenu-item-group > .munitmenu-content > .munitmenu-item {
  padding: 2px 8px 2px 28px;
  list-style: none;
}

.munitmenu-body .munitmenu-item .munitmenu-item-btn > i {
  visibility: hidden;
  color: #666;
  padding: 0 4px;
}

.munitmenu-body .munitmenu-item .munitmenu-item-btn.context-menu-active > i {
  visibility: visible;
}

.munitmenu-body .munitmenu-item:not(.munitmenu-item-group):hover .munitmenu-item-btn > i,
.munitmenu-body .munitmenu-item.munitmenu-item-group > .munitmenu-item:hover > .munitmenu-item-btn > i {
  visibility: visible;
  cursor: pointer;
}

/* context-menu-list */
.context-menu-list {
  position: absolute;
  display: inline-block;
  min-width: 160px;
  max-width: 160px;
  padding: 4px 0;
  margin: 0;
  list-style-type: none;
  background: #fff;
  border: 1px solid #dee2e6;
  border-radius: 3px;
  box-shadow: 0 2px 2px rgba(0,0,0,.25);
}

.context-menu-list > .context-menu-item {
  padding: 4px 0 4px 32px;
  font-size: 12px;
  font-weight: normal;
  color:#666;
}

.context-menu-list > .context-menu-item.context-menu-hover {
  color: #666;
  background-color: #f8f9fa;
}

.context-menu-list > .context-menu-item.context-menu-separator {
  padding: 0;
  margin: .35em 0;
  border-bottom: 1px solid #dee2e6;
}

.context-menu-icon.context-menu-hover:before,
.context-menu-icon.context-menu-icon--fa:before,
.context-menu-icon.context-menu-icon--fa.context-menu-hover:before {
  padding-left: 16px;
  padding-right: 4px;
  width: 12px;
  color: #666;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
}
