.pairselector-banned-action {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.7;
}

.pairselector-select, .pairselector-input {
  height: 20px;
  width: 100px;
  padding: 0 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.pairselector-select-left, .pairselector-input-left {
  height: 20px;
  width: 120px;
  padding: 0 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.pairselector-select-right, .pairselector-input-right {
  height: 20px;
  width: 80px;
  padding: 0 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.pairselector-select-sm, .pairselector-input-sm {
  width: 80px;
}

.pairselector-select-lg, .pairselector-input-lg {
  width: 120px;
}

.pairselector-container {
  padding: 2px 4px;
  width: 268px;
  border: 1px solid #a9a9a9;
}

.pairselector {
  width: 100%;
  height: 100%;
}

.pairselector-header .pairselector-title {
  padding: 2px 0;
  text-align: center;
}

.pairselector-item {
  line-height: 20px;
}

.pairselector-item .pairselector-td {
  padding: 2px 0;
}

.pairselector-btn {
  padding: 2px 4px;
  font-size: 12px;
  color: #666;
  border: none;
  border-radius: 3px;
}

.pairselector-btn:hover {
  background-color: #e3e6e6;
}

.pairselector-btn:active {
  background-color: #dee2e6;
}
