.msliders-container {
  width: 268px;
  min-height: 90px;
  border: 1px solid #a9a9a9;
}

.msliders-container > .msliders-item {
  padding: 4px;
}

.msliders-container > .msliders-item.item-sep {
  border-bottom: 1px solid #a9a9a9;
}

.msliders-header {
  display: flex;
  line-height: 20px;
  align-items: center;
}

.msliders-header > .msliders-title {
  margin-top: 2px;
  margin-right: 4px;
}

.msliders-header .msliders-input {
  height: 20px;
  width: 120px;
  padding: 0 4px;
  margin: auto 0;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.msliders-header .msliders-input ~ .msliders-icon {
  line-height: 20px;
  margin-left: 4px;
  color: #a9a9a9;
}

.msliders-header > .msliders-btns {
  margin: 2px 0;
  margin-left: auto;
}

.msliders-header > .msliders-btns .msliders-btn {
  padding: 2px 4px;
  font-size: 12px;
  color: #666;
  border: none;
  border-radius: 3px;
}

.msliders-header > .msliders-btns .msliders-btn:hover {
  background-color: #e3e6e6;
}

.msliders-header > .msliders-btns .msliders-btn:active {
  background-color: #dee2e6;
}

.msliders-body {
  padding-top: 4px;
}
