
.banned-action {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.3;
}

.mtextarea {
  width: 100%;
  height: 100%;
  text-align: center;
}

.mtextarea-input {
  width: 100%;
  padding: 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
  resize: vertical;
}

.mtextarea-input.noresize {
  resize: none;
}

.mtextarea-item {
  height: 100%;
  line-height: 20px;
}

.mtextarea-td.mtextarea-btns {
  min-width: 48px;
  text-align: center;
}

.mtextarea-btn {
  padding: 2px 4px;
  font-size: 12px;
  color: #666;
  border: none;
  border-radius: 3px;
}

.mtextarea-btn:hover {
  background-color: #e3e6e6;
}

.mtextarea-btn:active {
  background-color: #dee2e6;
}
