.mselector-banned-action {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.7;
}

.mselector-select, .mselector-input {
  height: 20px;
  width: 100px;
  padding: 0 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.mselector-select-sm, .mselector-input-sm {
  width: 80px;
}

.mselector-select-lg, .mselector-input-lg {
  width: 120px;
}

.mselector-container {
  padding: 2px 4px;
  width: 268px;
  border: 1px solid #a9a9a9;
}

.mselector {
  width: 100%;
  height: 100%;
}

.mselector-header .mselector-title {
  padding: 2px 0;
  text-align: center;
}

.mselector-item {
  line-height: 20px;
}

.mselector-item .mselector-td {
  padding: 2px 0;
}

.mselector-btn {
  padding: 2px 4px;
  font-size: 12px;
  color: #666;
  border: none;
  border-radius: 3px;
}

.mselector-btn:hover {
  background-color: #e3e6e6;
}

.mselector-btn:active {
  background-color: #dee2e6;
}