
.banned-action {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.3;
}

.moperator-select, .moperator-input {
  height: 20px;
  width: 100px;
  padding: 0 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.moperator-container {
  padding: 2px 4px;
  margin: 0;
  width: 268px;
  border: 1px solid #a9a9a9;
}

.moperator {
  width: 100%;
  height: 100%;
}

.moperator-header .moperator-title {
  padding: 2px 0;
  text-align: center;
}

.moperator-item {
  line-height: 20px;
}

.moperator-item .moperator-td {
  padding: 2px 0;
}

.moperator-btn {
  padding: 2px 4px;
  font-size: 12px;
  color: #666;
  border: none;
  border-radius: 3px;
}

.moperator-btn:hover {
  background-color: #e3e6e6;
}

.moperator-btn:active {
  background-color: #dee2e6;
}
