
.banned-action {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.3;
}

.mtextinput {
  width: 100%;
  height: 100%;
  text-align: center;
}

.mtextinput-input {
  height: 20px;
  width: 100%;
  padding: 0 4px;
  line-height: 1;
  border: 1px solid #a9a9a9;
  border-radius: 0;
}

.mtextinput-item {
  height: 100%;
  line-height: 20px;
}

.mtextinput-td.mtextinput-btns {
  min-width: 48px;
  text-align: center;
}

.mtextinput-btn {
  padding: 2px 4px;
  font-size: 12px;
  color: #666;
  border: none;
  border-radius: 3px;
}

.mtextinput-btn:hover {
  background-color: #e3e6e6;
}

.mtextinput-btn:active {
  background-color: #dee2e6;
}
